import React from 'react';
import { Card, Row, Col, Select, Button, DatePicker, Alert, message } from 'antd';
import reportGIF from '../../default_report.gif'
import './index.css'
import moment from 'moment';
import axios from '../../config/axios'

const { Option } = Select;
const { RangePicker } = DatePicker;

const optionsData = [
    'Daily', 'Weekly Leave', 'Monthly', 'Annual', 'Other'
];
const reportTypesData = {
    Daily: ["Pending Leave", "Approved Leave", "Absentees", "Head Count"],
    Weekly: ["Weekly Leave"],
    Monthly: ["Resign", "Leave", "No pay", "Special Leave", "Lieu Leave", "Recruitment", "Resign Ratio", "Recruitment Ratio"],
    Annual: ["Resign", "Leave", "No pay", "Resign Ratio", "Recruitment Ratio", "No of Recruitment Summary"],
    Other: ["Total Male Count", "Total Female Count", "Age Analysis", "Department Wise Employee", "Current Total Interns", "Total Probation Emp", "Total Contract Emp", "Total Permanent Emp", "Total Headcount"]
}

class Reports extends React.Component {

    constructor(props) {
        super(props);
        this.state = {
            selectedOption: optionsData[0],
            reports: reportTypesData[optionsData[0]],
            selectedReportType: reportTypesData[optionsData[0]][0],
            reportTypeVisible: true,
            isopen: false,
            time: moment(),
            datePickerDefault: moment(),
            selectedDates: null,
            error: false,
        };
        this.reset = this.reset.bind(this);
        this.download = this.download.bind(this);
        this.onPickerChange = this.onPickerChange.bind(this);
    }

    handleOptionChange = selectedOption => {
        this.setState({ selectedOption }, () => {// reset stored date value
            if (this.state.selectedOption === "Annual") {
                this.setState({ selectedDates: [moment().year()] })
            } else if (this.state.selectedOption === "Monthly") {
                this.setState({ selectedDates: [moment().subtract(31, 'd').format('YYYY-MM-DD'), moment().format('YYYY-MM-DD')] })
            } else if (this.state.selectedOption === "Weekly Leave") {
                this.setState({ selectedDates: [moment().subtract(7, 'd').format('YYYY-MM-DD'), moment().format('YYYY-MM-DD')] })
            } else if (this.state.selectedOption === "Daily") {
                this.setState({ selectedDates: [moment().format('YYYY-MM-DD')] })
            }
        });
        this.setState({ reports: reportTypesData[selectedOption] })
        if (selectedOption !== "Weekly Leave") {
            this.setState({ selectedReportType: reportTypesData[selectedOption][0] })
            this.setState({ reportTypeVisible: true })
        }
        else { this.setState({ reportTypeVisible: false }) }
    };

    handleReportTypeChange = selectedReportType => {
        this.setState({ selectedReportType })
    }

    onPickerChange(date, dateString) {
        if (this.state.selectedOption === "Weekly Leave") {
            this.setState({ selectedDates: dateString })
        } else if (this.state.selectedOption === "Monthly") {
            this.setState({ selectedDates: dateString })
        } else {
            this.setState({ selectedDates: [dateString] })
        }
        this.setState({ datePickerDefault: date })
    }

    reset() {
        this.handleOptionChange(optionsData[0])
        this.setState({ datePickerDefault: moment() })
    }

    download() {
        if (!this.state.selectedDates) {
            if (this.state.selectedOption === "Annual") {
                this.setState({ selectedDates: [moment().year()] }, () => {
                    this.generateReport();
                })
            } else if (this.state.selectedOption === "Monthly") {
                this.setState({ selectedDates: [moment().subtract(31, 'd').format('YYYY-MM-DD'), moment().format('YYYY-MM-DD')] }, () => {
                    this.generateReport();
                })
            } else if (this.state.selectedOption === "Weekly Leave") {
                this.setState({ selectedDates: [moment().subtract(7, 'd').format('YYYY-MM-DD'), moment().format('YYYY-MM-DD')] }, () => {
                    this.generateReport();
                })
            } else if (this.state.selectedOption === "Daily") {
                this.setState({ selectedDates: [moment().format('YYYY-MM-DD')] }, () => {
                    this.generateReport();
                })
            }
        } else {
            this.generateReport();
        }

    }

    generateReport() {
        this.setState({ error: false });
        console.log(this.state.selectedOption);
        console.log(this.state.selectedReportType);
        console.log(this.state.selectedDates);
        axios.post('reports/', {
            "selectedOption": this.state.selectedOption,
            "selectedReportType": this.state.selectedReportType,
            "selectedDates": this.state.selectedDates
        },
            {
                headers: {
                    Authorization: 'Bearer ' + localStorage.getItem("header")
                },
                responseType: "blob"
            })
            .then(res => {
                var fileURL = window.URL.createObjectURL(new Blob([res.data]));
                var fileLink = document.createElement("a");
                fileLink.href = fileURL;
                fileLink.setAttribute("download", this.state.selectedOption + " Report.xlsx");
                document.body.appendChild(fileLink);
                fileLink.click();
            })
            .catch(e => {
                this.setState({ error: true });
                message.error("Error");
                console.log(e);
            })
    }

    render() {
        return (
            <Row gutter={16} type="flex">
                <Col xs={24} lg={12} className="equalHeight">
                    <Card type="inner" title="Report Generation" hoverable='true' className="fullWidth">
                        <Row>
                            <Col span={24}>
                                Choose type:
                            </Col>
                            <Col span={24}>
                                <Select
                                    style={{ width: '100%' }}
                                    defaultValue={optionsData[0]}
                                    value={this.state.selectedOption}
                                    onChange={this.handleOptionChange}
                                >
                                    {optionsData.map(option => (
                                        <Option key={option}>{option}</Option>
                                    ))}
                                </Select>
                            </Col>
                        </Row>
                        {this.state.reportTypeVisible ?
                            <div>
                                <br />
                                <Row>
                                    <Col span={24}>
                                        Choose Report:
                                    </Col>
                                    <Col span={24}>
                                        <Select
                                            style={{ width: '100%' }}
                                            value={this.state.selectedReportType}
                                            onChange={this.handleReportTypeChange}
                                        >
                                            {this.state.reports.map(reportType => (
                                                <Option key={reportType}>{reportType}</Option>
                                            ))}
                                        </Select>
                                    </Col>
                                </Row>
                            </div>
                            : null}
                        {this.state.selectedOption === "Daily" ?
                            <div>
                                <br />
                                <Row>
                                    <Col span={24}>
                                        Choose Date:
                                    </Col>
                                    <Col span={24}>
                                        <DatePicker allowClear={false} value={this.state.datePickerDefault} defaultValue={moment()} onChange={this.onPickerChange} style={{ width: '100%' }}
                                        />
                                    </Col>
                                </Row>
                            </div>
                            : null}
                        {this.state.selectedOption === "Weekly Leave" ?
                            <div>
                                <br />
                                <Row>
                                    <Col span={24}>
                                        Choose Range:
                                    </Col>
                                    <Col span={24}>
                                        <RangePicker allowClear={false} defaultValue={[moment().subtract(7, 'd'), moment()]} onChange={this.onPickerChange} placeholder="Select Week" style={{ width: '100%' }} />
                                    </Col>
                                </Row>
                            </div>
                            : null}
                        {this.state.selectedOption === "Monthly" ?
                            <div>
                                <br />
                                <Row>
                                    <Col span={24}>
                                        Choose Month:
                                    </Col>
                                    <Col span={24}>
                                        <RangePicker allowClear={false} defaultValue={[moment().subtract(31, 'd'), moment()]} onChange={this.onPickerChange} placeholder="Select Month" style={{ width: '100%' }} />
                                    </Col>
                                </Row>
                            </div>
                            : null}
                        {/* https://juejin.cn/post/6844903859987415047 */}
                        {this.state.selectedOption === "Annual" ?
                            <div>
                                <br />
                                <Row>
                                    <Col span={24}>
                                        Choose Year:
                                    </Col>
                                    <Col span={24}>
                                        <DatePicker
                                            value={this.state.time}
                                            open={this.state.isopen}
                                            mode="year"
                                            format="YYYY"
                                            onOpenChange={(status) => {
                                                if (status) {
                                                    this.setState({ isopen: true })
                                                } else {
                                                    this.setState({ isopen: false })
                                                }
                                            }}
                                            onPanelChange={(v) => {
                                                this.setState({
                                                    time: v,
                                                    selectedDates: v.year(),
                                                    isopen: false
                                                })
                                            }}
                                            onChange={() => {
                                                this.setState({ time: null })
                                            }}
                                            style={{ width: '100%' }}
                                            allowClear={false}
                                        />
                                    </Col>
                                </Row>
                            </div>

                            : null}

                        <Row type="flex" justify="center">
                            <Col>
                                <br />
                                <Button type="primary" icon='download' size="large" onClick={this.download}>
                                    Download
                                </Button>
                            </Col>
                            &nbsp;&nbsp;&nbsp;
                            <Col>
                                <br />
                                <Button type="danger" size="large" icon='close' onClick={this.reset}>
                                    Reset
                                </Button>
                            </Col>
                        </Row>
                        {this.state.error === true ?
                            <Alert message="Error" type="error" showIcon closable /> : null}
                    </Card >
                </Col>
                <Col xs={24} lg={12} className="equalHeight">
                    <Card type="inner" hoverable='true' >
                        <Row>
                            <Col>
                                <img src={reportGIF} alt="loading..." id="aspectRatio" />
                            </Col>
                        </Row>
                    </Card>
                </Col>
            </Row>
        )
    }
}

export default Reports;